.gallery {
    padding: 0;
    margin: 0;
    list-style: none;
    max-width: calc(100% - 64px);
    margin-left: auto;
    margin-right: auto;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 24px;
  }
  
  .gallery__item {
    padding: 30px;
  }
  
  .gallery__item p {
    margin-top: 0;
    margin-bottom: 8px;
  }
  
  .gallery__thumb {
    height: 200px;
    margin-bottom: 16px;
  }
  
  .gallery__item img {
    object-fit: cover;
    max-width: 100%;
    height: 100%;
  }